@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
body {
  font-size: 1.2em;
  font-family: 'Roboto', sans-serif; }

/** Responisve css **/
@media only screen and (max-width: 500px) {
  body {
    font-size: 1em; }
  main {
    height: calc(100vh - 300px) !important; }
  .Footer {
    position: fixed !important;
    bottom: -10px !important; }
  .searchContainer,
  .carousel,
  .keyFeatures,
  .FooterCnt ul {
    display: none; }
  .header {
    width: 100%; }
    .header .logged-header-mob {
      width: 100%;
      position: relative;
      text-align: center;
      padding-bottom: 5px;
      border-bottom: 1px solid #bcbcbc; }
      .header .logged-header-mob .main-menu {
        float: left; }
      .header .logged-header-mob .notification-icon {
        float: right; }
    .header .logo {
      width: auto; }
  .bgWhite {
    background: #fff;
    padding: 10px;
    border-radius: 0px !important;
    border: 1px solid #dcdcdc; }
    .bgWhite #forgotPWD {
      padding-top: 20px;
      margin-bottom: 0px;
      font-size: 14px; }
    .bgWhite #signUp {
      display: none; }
  .form-control {
    border: none !important;
    border-bottom: 1px solid #ced4da !important;
    border-radius: 0px !important; }
  .btn-block {
    width: auto !important;
    float: right;
    padding: 8px 40px !important; }
  p {
    clear: both;
    margin-top: 5px; }
  .container-fluid {
    position: relative;
    top: calc(50vh - 200px);
    z-index: 2; }
  .globalLinks-mob {
    display: block !important; }
    .globalLinks-mob ul,
    .globalLinks-mob li {
      list-style-type: none; }
    .globalLinks-mob li {
      float: left;
      padding: 0px 20px;
      border-right: 1px solid #ced4da; }
      .globalLinks-mob li a {
        color: #666666;
        font-size: 14px; }
    .globalLinks-mob li:last-child {
      border-right: none; } }

/** Styles for 5 and six inch devices */
